import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import hUpload from '@/components/h-upload/h-upload.vue';
export default {
	components: {
		uniNavBar,
		hUpload
	},
	data() {
		return {
			type1: [],
			type2: [],
			type2Index: 0,
			dates: '',
			upData: {
				type: '0',
				category: '0',
				time: '点击选择',
				address: {
					address_content: '选择地点',
					longitude: '',
					latitude: ''
				},
				content: '',
				imgList: []
			}
		};
	},
	onReady() {
		var dateee = new Date().toJSON();
		this.dates = new Date(+new Date(dateee) + 8 * 3600 * 1000)
			.toISOString()
			.replace(/T/g, ' ')
			.replace(/\.[\d]{3}Z/, '');
	},

	methods: {
		schange(val) {
			console.log(111);
			console.log(val);
		},
		setAttachData(val) {
			console.log(222);
			console.log(val);
			this.upData.imgList = val;
		},
		updataJump() {
			console.log('上报提交');

			console.log(this.upData);
			uni.showLoading({
				title: 'loading'
			});
			this.$http
				.post('/interface/rest/http/xlwb/xlgc-wb-xcx-grzx-wysb.htm', this.upData, { params: {} })
				.then(res => {
					uni.hideLoading();
					console.log('上报结果++++++');
					console.log(res);
					if (res.data.msgState == 1) {
						this.cleanData();
					}
					uni.showToast({
						title: res.data.msg,
						duration: 2000
					});
				})
				.catch(err => {
					console.log(err);
					uni.hideLoading();
				});
			// setTimeout(() => {
			// 	uni.hideLoading();
			// 	uni.showToast({
			// 	    title: '上报成功',
			// 	    duration: 2000
			// 	});
			// 	this.cleanData();
			// }, 1000);
		},
		ontrueGetList() {
			//获取上报类型
			this.getType();
			//获取上报事件
			//this.getType1()
		},
		getType() {
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-sblxxz.htm', { params: {} })
				.then(res => {
					console.log(res);
					if (res.data.msgState == 1) {
						var obj1 = [{ id: '0', type: '点击选择' }];
						var data = res.data.list;
						var obj = [...obj1, ...data];
						this.type1 = this.typeChange(obj);
						this.type2 = this.typeChange(obj);
					} else {
						uni.showLoading({
							title: '请求失败'
						});
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		getType1() {
			this.$http
				.get('/interface/rest/http/xlwb/xlgc-wb-xcx-sbsjclzt.htm', { params: {} })
				.then(res => {
					console.log('上报事件');
					console.log(res);
					if (res.data.msgState == 1) {
						// 	var obj1=[{id:'0',type:"点击选择"}]
						// 	var data=res.data.list
						// 	var obj=[...obj1,...data]
						// this.type1=	this.typeChange(obj)
					} else {
						uni.showLoading({
							title: '请求失败'
						});
					}
				})
				.catch(err => {
					console.log(err);
				});
		},
		//处理函数
		typeChange(val) {
			var arr = [];
			for (let item in val) {
				console.log(val[item].type);
				arr.push(val[item].type);
			}

			return arr;
		},
		cleanData() {
			this.$refs.upload.cleanAll();
			var upData = {
				type: '0',
				category: '0',
				time: '点击选择',
				address: {
					address_content: '选择地点',
					longitude: '',
					latitude: ''
				},
				content: '',
				imgList: []
			};
			this.upData = upData;
		},
		sizeTypeChange(e) {
			this.upData.type = parseInt(e.detail.value);
		},
		sizeType2Change(e) {
			this.upData.category = parseInt(e.detail.value);
		},

		chooseLocation: function() {
			uni.chooseLocation({
				success: res => {
					console.log(res.name);
					console.log(res.address);
					console.log(res.longitude);
					console.log(res.latitude);
					// this.location = formatLocation(res.longitude, res.latitude),
					this.upData.address.address_content = res.name;
					this.upData.address.longitude = res.longitude;
					this.upData.address.latitude = res.latitude;
				}
			});
		},
		showPicker() {
			this.$refs.date1.show();
		},
		onConfirm(res, type) {
			this.upData.time = res.result;
			console.log(res.result);
		},
		//上传图片/视频
		upLoadImg() {
			console.log('根据选择调用不同的api');
		}
	}
};
